<template>
<div class="flex flex-col justify-center items-center pt-14 gap-y-7">
  <div class="text-3xl font-bold">
    Change Your Dapp Info
  </div>
  <div class="w-3/5">
    <el-form :model="ruleForm" status-icon ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="url" prop="">
        <el-input type="text" v-model="ruleForm.url"></el-input>
      </el-form-item>
      <el-form-item label="logoimg" prop="">
        <el-input type="text" v-model="ruleForm.logoimg"></el-input>
      </el-form-item>
      <el-form-item label="name" prop="">
        <el-input type="text" v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="description">
        <el-input v-model.number="ruleForm.description" type="textarea"></el-input>
      </el-form-item>
      <el-form-item class="inline-block text-center">
        <el-button  type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</div>
</template>

<script>
import {mapState} from "vuex";

mapState
export default {
  name: "change_site",
  mounted(){
    let params=this.$route.query
    console.log("完整的params",this.$route)
    console.log("params is:",params)
    let sitid=this.$route.query.sitid
    let url=this.$route.query.url
    let name=this.$route.query.name
    let logoimg=this.$route.query.logoimg
    let description=this.$route.query.description

    this.ruleForm={
      sitid:sitid,
          url:url,
        logoimg:logoimg,
        name:name,
        description:description
    }
    console.log("this.ruleForm",this.ruleForm)
  },
  // props:["id","url","logoimg","name","description"],
  data(){
    return {
      ruleForm:{}
    }
  },
  computed:{
    ...mapState(["signedContract"])
  },
  methods:{
    async submitForm(formName) {
      let name=this.ruleForm.name
      let url=this.ruleForm.url
      let description=this.ruleForm.description
      let logoimg=this.ruleForm.logoimg
      let sitid=this.ruleForm.sitid
      console.log(this.signedContract)

      await this.signedContract.change(sitid,url,name,description,logoimg)
      this.resetForm("ruleForm")
      this.$message({
        message: '恭喜你，修改dapp到网站成功',
        type: 'success'
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

